import { UserSession } from './../../core/sessions';
import { Component, ViewChild } from '@angular/core';
import { HttpCommonUtilsService } from '../../core/http_wrapper/http-common-utils.service';
import { HttpSuccesFailureResponse } from '../../core/http_wrapper/http_wrapper_response';
import { ScheduleMapper } from '../../core/components/schedule/schedule.mapper';
import { ScheduleEvents } from '../../core/components/schedule/schedule.model';
import { Schedule } from '../../core/components/schedule/schedule';
import { Constants } from '../../core/constants';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpUserAuthUtilsService } from '../../core/http_wrapper/http-userauth-utils.service';
import { ClassDetail } from '../../core/models/classListResponse';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { EditclasspopupComponent } from '../../editclasspopup/editclasspopup.component';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { LazyLoadScriptService } from 'lazy-load-script/lazy-load-script.service';

declare var $: any;
@Component({
  selector: '',
  templateUrl: './gymschedule.tpl.html'
})
export class GymScheduleComponent {
  events: any[] = [];
  headerConfig: any;
  filterData: Array<any> = [];
  trainerFilterData: Array<any> = [];
  start: number;
  end: number;
  endDate: Date;
  startDate: Date;
  private filter: string = '';
  private branchFilter: string = "";
  private trainerFilter: string = '';
  showStub: boolean = false;
  previousHandler = () => {
    this.updateEventData(-7);
  };
  nextHandler = () => {
    this.updateEventData(7);
  }
  @ViewChild(Schedule) schedule: Schedule;
  private userDetails = JSON.parse(localStorage.getItem('User'));
  public currentDate;
  component = this;
  firstDay:number=0;
  classType="upcomming";
  loadedJsCount=0;
renderUI = false;
  constructor(private _common_http: HttpCommonUtilsService,
    private _mapper: ScheduleMapper,
    private _activatedRoute: ActivatedRoute,
    private _httpUserAuth: HttpUserAuthUtilsService,
    private modal: Modal,private layLoadScript: LazyLoadScriptService,
    private router: Router

  ) { }


  ngOnInit() {
    setTimeout(() => {
      this.renderUI = true;
      this._httpUserAuth.getTrainerMyClassesDetail("",LocalStorageService.getUserId(),0,this.classType,this,true)
      this.headerConfig = {
        left: 'prev,next',
        center: 'title',
        right: ''
      };
    let today = new Date();
    this.firstDay=today.getDay();
   this.currentDate=UserSession.getFormatedDate(today);
      this.initializeDates();
      setTimeout(() => {
        this.bindEvents();
        document.getElementById("scheduleWrapper").style.visibility = "visible";
      }, 500);
      this._common_http.getTrainerList(
        "&branchId=" + "-1" +"&gymId=" + this.userDetails.data.id,
        this,
        false
      );
      this._common_http.getBranchFilter(this.userDetails.data.id, this, false);
      this._activatedRoute.params.subscribe(params => {
          if (params["sessionId"]) {
            this._httpUserAuth.getNewClassDetailList(params["sessionId"], null, this, true);
          }
        });  
    }, 2000);
    
  }
  public init(){
    this.layLoadScript.jqueryList.forEach(element => {
      this.layLoadScript
      .loadScript(element).subscribe(_ => {
        this.loadedJsCount++
          console.log('Jquery is loaded!'+this.loadedJsCount+":"+this.layLoadScript.jqueryList.length)
      });
  });
  }

  refreshList() {
    this.updateEventData(0);
  }
  bindEvents() {
    const that = this;
    $('.fc-prev-button').unbind("click",this.previousHandler);
    $('.fc-next-button').unbind("click", this.nextHandler);
    $('.fc-prev-button').bind("click",this.previousHandler);
    $('.fc-next-button').bind("click", this.nextHandler);
  }

  onBranchFilterChange(event: number) {
    if (event != -1) {
      this.filter = "&branchIdList=" + event;
    }else{
      this.filter='';
    }

    this.branchFilter = this.filter;
    let today = new Date();
    this._common_http.getEvents(
      this.startDate,
      this.endDate,
      this.userDetails.data.userCategory,
      this.userDetails.data.id,
      this,
      true,
      this.filter
    );
    this._common_http.getTrainerList(
      "&branchId=" + event + "&gymId=" + this.userDetails.data.id,
      this,
      false
    );
  }

  onTrainerFilterChange(event: number) {
    if (event != -1) {
      if (this.filter.indexOf("trainerIdList") == -1) {
        this.filter = this.filter + "&trainerIdList=" + event;
      }
      else {
        this.filter = this.branchFilter + "&trainerIdList=" + event;
      }
    }
    else {
      this.filter = '';
    }

    let today = new Date();
    this._common_http.getEvents(
      this.startDate,
      this.endDate,
      this.userDetails.data.userCategory,
      this.userDetails.data.id,
      this,
      true,
      this.filter
    );
  }
    private initializeDates(callback?: Function, date?: any) {
      let today;
      if (date) {
        today = date;
      }
      else {
        today = new Date();
      }
      //this.start = today.getDate() - today.getDay();
        this.start = today.getDate();

      this.end = this.start + 6;

      this.startDate = new Date(today.setDate(this.start));
      this.endDate = new Date(today.setDate(this.end));
      this._common_http.getEvents(this.startDate,this.endDate,this.userDetails.data.userCategory,this.userDetails.data.id,this,true, this.filter);
    }
    
  private updateEventData(delta: number) {
    this.start = this.start + delta;
    this.end = this.end + delta;
    let today = new Date();
    this.startDate.setDate(this.startDate.getDate() + delta);
    this.endDate.setDate(this.endDate.getDate() + delta);
    this._common_http.getEvents(this.startDate, this.endDate, this.userDetails.data.userCategory, this.userDetails.data.id, this, true, this.filter);
    this.schedule.updateMonthYear(this.endDate);

  }

  isLoggedIn() {
    return UserSession.isLoggedIn();
  }
  openparticipantslist(gymSessionObj: ClassDetail) {
    let parent = this;
    try{
      return this.modal.open(EditclasspopupComponent, overlayConfigFactory({ gymSessionObj, parent }, BSModalContext))

    }catch(e){
      console.log(e);
    }
  }
  onCreateNewSessionClick(event:any){
    this.router.navigate(['/sessionCreate']);
  }
  

  onSuccess(type, responseData) {
    
    if (type == Constants.TrainerClassesDetailApi) {
      if (responseData.data.length == 0) {
        this.showStub = true;
      }
    }
    if (type == Constants.classSessionDetailApi) {
        this.openparticipantslist(responseData.data);
       }
    if (type == Constants.getEventsApi) {
      this.events = this._mapper.mapEventsData(responseData.data);
      let today = new Date();
    }
    if (type == Constants.trainerFilterListApi) {
      this.trainerFilterData = responseData.data;
      setTimeout(() => {
        this.bindEvents();
      }, 500);
    }

    if (type == Constants.branchFilterListApi) {
      this.filterData = responseData.gymBranchResponseList;
    }

  }
  onFailure(response: string) {
    throw response;
  }
  handleEventClick(event) {

  }
  onMonthChange(event) {
    // this.initializeDates(null,new Date(event));
    // let date = new Date(event);

    // let day: number = 0;
    // if (date.getDay() != 0) {
    //   day = (date.getDay() - 1)
    // }
    // else {
    //   day = 6;
    // }
    // this.startDate = new Date(date.setDate((date.getDate() - day)));
    // this.endDate = new Date(this.startDate.toString());
    // this.endDate.setDate(this.endDate.getDate() + 6);
    let calendar = this.schedule.schedule.fullCalendar('getCalendar');
    let view = calendar.view;
    this.startDate = new Date(view.start._i);
    this.endDate = new Date((view.end._i - (24*60*60*1000)));
    this._common_http.getEvents(this.startDate, this.endDate, this.userDetails.data.userCategory, this.userDetails.data.id, this, true, this.filter);
    this.schedule.updateMonthYear(this.endDate);
  }
}



